body{background:#edf0f8; font-family: "OpenSans";}

.App{padding:2%;}
.App:before{clear: both; content:''; display:flex;}
.poster-card{width:46%; margin:2%; float:left; background:#fff; position:relative; padding:40px 40px 40px 230px; box-sizing: border-box; height:274px; border-radius:8px; box-shadow:2px 4px 14px rgba(0,0,0,0.1); line-height: 1.5em; flex:1 1 calc(100% - 50%);}
.poster-card-item:nth-child(odd) {flex: 0 0 50%;}
.poster-card > div > h1{line-height:1.2em; color:#3c3950;}
.poster-card > img{position:absolute; left:23px; top:-23px; width:180px;box-shadow:2px 4px 14px rgba(0,0,0,0.1);}
.poster-card > div > p{color:#b5b3bd;}
.poster-card > div > span{color:#FF9800;}

@media screen and (min-width:320px) and (max-width:667px){
    .poster-card{
        width:98%;
        flex-direction: column;
        margin:4% 2%;
    }
}